
import React, { useRef, useState, useEffect } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "./App.css";
import Detection from './ObjectDetection/detection.js';
import Home from './HomePage/Home.js';
import fire from './fire.js';
import Login from './Login/Login.js';
function App() {

  const[user,setUser] = useState('');
  const[email,setEmail] = useState('');
  const[password,setPassword] = useState('');
  const[emailError,setEmailError] = useState('');
  const[passwordError,setPasswordError] = useState('');
  const[hasAccount,setHasAccount] = useState(false);

  const clearInputs = () => {
    setEmail('');
    setPassword('');
  }

  const clearErrors = () => {
    setEmailError('');
    setPasswordError('');
  }

  const handleLogin = () => {
    clearErrors();
    fire
      .auth()
      .signInWithEmailAndPassword(email, password)
      .catch(err => {
        switch(err.code){
          case "auth/invalid-email" : 
          case "auth/user-disabled" :
          case "auth/user-not-found":
            setEmailError(err.message); 
            break;
          case "auth/wrong-password":
            setPasswordError(err.message);
            break;   
        }
      })
  }
  const handleSignup = () => {
    clearErrors();
    fire
      .auth()
      .createUserWithEmailAndPassword(email, password)
      .catch(err => {
        switch(err.code){
          case "auth/email-already-in-use" : 
          case "auth/invalid-email" :
            setEmailError(err.message); 
            break;
          case "auth/weak-password":
            setPasswordError(err.message);
            break;   
        }
      })
  }

  const handleLogout = () => {
    fire.auth().signOut();
  }

  const authListener = () => {
    fire.auth().onAuthStateChanged(user => {
      if(user){
        clearInputs();
        setUser(user);
      }
      else
      setUser('');
    })
  };

  useEffect(() => {
    authListener();
  }, [])

  return (
    // <Router>
    //   <Switch> 
    //     <Route path = "/" exact  component = {Home} />
    //     <Route path = "/detection" exact  component = {Detection} />
    //   </Switch>  
    // </Router>
    <div>
      {user ? (
        <Router>
        <Switch> 
          <Route path = "/real-time-object-detection" exact  render={() => (
            <Home handleLogout = {handleLogout}/>
          )} />
          <Route path = "/real-time-object-detection/detection" exact  component = {Detection} />
        </Switch>  
      </Router>
      // <Home handleLogout = {handleLogout}/>
      ) : (
        <Login 
          email = {email}
          setEmail = {setEmail}
          password = {password}
          setPassword = {setPassword}
          handleLogin = {handleLogin}
          handleSignup = {handleSignup}
          hasAccount = {hasAccount}
          setHasAccount = {setHasAccount}
          emailError = {emailError}
          passswordError = {passwordError}
        />

      )}
    </div>
  );
}

export default App;
