import React from 'react';
import { Link } from "react-router-dom";
import './Home.css';
import logo from '../Images/AI-logo.jpg';
import logo_tensorflow from '../Images/logo_tensorflow.png';
import logo_deep_learning from '../Images/logo_deep_learning.png';

const Home = ({ handleLogout }) => {
    return (<React.Fragment>
    <div id="navbar_home">
        <div id="navbar_stick">
            <div id="navbar">
                <div id="logo">
                    <img src={logo} alt="Deep Learning Projects" />
                </div>
                <ul>
                    <li class="item"><a href="#home">Home</a></li>
                    <li class="item"><a href="#projects_container">Projects</a></li>
                    <li class="item"><a href="#about_us">About Us</a></li>
                    <li class="item"><a href="#contact_us">Contact Us</a></li>
                </ul>
                <button onClick = {handleLogout}>Logout</button>
            </div>
        </div>
        <section id="home">
            <h1 class="primary_heading">Welcome to Deep Learning Projects</h1>
            <p id = "navbar_para">Test some of our Projects here</p>
        </section>
    </div>

    <section class="projects_container" id="projects_container">
        <h1 class="primary_heading center">Our Projects</h1>
        <div id="projects">
            <Link to='/real-time-object-detection/detection' style={{ textDecoration: `none` }}> 
            <div class="box" id = "project1">
                <img src={logo_tensorflow} alt="Object Detection Project" />
                <h2 class="secondary_heading center">Real Time Object Detection </h2>
                <p class="center">This application gets access to the webcam and detect the objects visible in Camera</p>
            </div> 
            </ Link> 
            <div class="box" id = "project2">
                <img src={logo_deep_learning} alt="Deep Learning Projects" />
                <h2 class="secondary_heading center">Real time Facial Emotion Recognition</h2>
                <p class="center">As the name suggests it recognises the one's facial expressions.</p>
            </div>
        </div>
    </section>
    <div id="contact_us_image">
        <section id="contact_us">
            <h1 class="primary_heading center">Contact Us</h1>
            <h2 class="seconday_heading center">We'll reach you</h2>
            <div id="contact">
                <form>
                    <div class="form-group">
                        <label for="name" class="center">Your Name </label>
                        <input type="name" name="my_name" id="name" placeholder="Enter your name here" />
                    </div>
                    <div class="form-group">
                        <label for="email" class="center">Your Email I'd </label>
                        <input type="email" name="my_email" id="email" placeholder="Enter your email id here" />
                    </div>
                    <div class="form-group">
                        <label for="phone" class="center">Your Contact Number </label>
                        <input type="phone" name="my_phone" id="phone" placeholder="Enter your mobile number here" />
                    </div>
                    <div class="form-group">
                        <label for="message" class="center">Your message </label>
                        <textarea name="my_message" id="message" cols="30" rows="10"></textarea>
                    </div>
                    <div class="form-group">
                        <input type="submit"  class="btn" />
                    </div>
                </form>
            </div>
        </section>
    </div>
    </React.Fragment>

    );
};

export default Home;